import React, { useRef, useState } from "react";
import './Slider.scss'
const Slider = ({ cardData }) => {
  return (
   
    <>
      { cardData.map((curElem) => {
         return(
           <> 
              <div className="slider">      
                 <img src={curElem.image}  alt="" className="main-image"/> 
                 <button class='button'></button>
                 <img src={curElem.logo} alt="" className="logo"/> 
                 <p className="h5"> {curElem.name} </p>   
               </div>             
            </>
        )
      })}  
    </>
  )
}

export default Slider