import React from 'react'
import './Profile.scss'
const Profile = ({ MinorData ,id}) => {
    return (
      <>
          {
            MinorData.map((curElem, index) => {
              if (curElem.id == id)
              {
                  return(
                   <>
                       <div className="profile">
                           <img src={ curElem.image } alt="" />
                           <div className="profile-text">
                               <p className='h4'>{ curElem.name }</p>
                               <p className='body'>{ curElem.prof }</p>
                           </div>
                           <div className="btn1"><a href="#" className='link'>Follow</a></div>
                       </div>
                   </>
                  )
              }
            })
          }
      </>
    )
  }

export default Profile