import React from 'react'
import './ImgCard.scss'
import img1 from '../../assets/images/image16.png'

const imgCard = ({ MinorData ,id}) => {
  return (
    <>
        {
          MinorData.map((curElem, index) => {
            if (curElem.id == id)
            {
                return(
                 <>
                     <div className="image-card1">
                       <img src={curElem.image} alt="" className='card_image'/>
                       <div className="overlay"></div>
                       <div className="new-img">
                          <p className='new'>NEW</p>
                       </div>
                       <p className='s3'>{curElem.prof}</p>
                       <p className='h2'>{curElem.name}</p>
                       <div className="btn1"><a href="#" className='link'>Follow</a></div>
                     </div>
                 </>
                )
            }
          })
        }
    </>
  )
}

export default imgCard